import { Col, Row } from "antd";
import { Icon as LegacyIcon } from "@ant-design/compatible";
import React from "react";
import { CList } from "../..";

const CDetailMenuData = ({ data }) => {
  return (
    <Row>
      <Col span={24}>
        <CList label="Label" value={data.label} color="#D3D3D3" />
        <CList
          label="Icon"
          value={<LegacyIcon type={data.icon} />}
          color="#FFFFFF"
        />
        <CList label="Path" value={data.path} color="#D3D3D3" />
      </Col>
    </Row>
  );
};

export default CDetailMenuData;
